import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, NgForm, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {
  dataRestore: any = {}
  public listCustomer: any[] = this.vhQuerySales.getlocalCustomers().filter(item => item.type != 3).filter(customer => {
    if (customer['_id'] != "id_retail") {
      if (customer['debt_enable'] == true) return true
      else return false
    } return false
  });
  customerShow: any = []
  signInForm: FormGroup;
  listShop: Array<any> = this.vhQuerySales.getlocalShopListSendo()
  existShop: boolean = false; //khi shop key tồn tại trong db thì trường này bằng true
  loading: boolean = false;
  constructor(
    private router: Router,
    private vhQuerySales: VhQuerySales,
    private vhComponent: VhComponent,
    private lang: LanguageService,
    private vhAlgorithm: VhAlgorithm
  ) {
    this.customerShow = [...this.listCustomer]
  }

  ngOnInit(): void {
    this.initForm()
  }
  initForm() {
    this.signInForm = new FormGroup({
      shop_key: new FormControl(''),
      secret_key: new FormControl('', [Validators.required]),
      name: new FormControl('', [Validators.required]),
      id_customer: new FormControl(null, [Validators.required]),
    })
  }
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/settings'], { state: { dataRestore: this.dataRestore } })
  }
  onSubmit(): void {
    let form = this.signInForm.value
    this.vhComponent.alertConfirm(this.lang.translate("Confirm"), "", this.lang.translate("Do you want to connect with Sendo") + "?", "Ok", this.lang.translate("Cancel")).then(() => {
      let data = { date: new Date().toISOString(), name: form['name'], id_customer: form.id_customer }
      this.vhComponent.showLoading("", "transparent-loading").then(() => {
        this.vhQuerySales.signInSendo(form['shop_key'], form['secret_key'], data).then(res => {
          this.vhComponent.hideLoading(0).then(() => {
            if (res) {
              this.dataRestore.haveUpdate = true
              this.vhComponent.showToast(1500, this.lang.translate("Successfully connected Sendo"), "success-toast")
              this.goBack()
            } else
              this.vhComponent.showToast(1500, this.lang.translate("This shop existed, shop key or secret_key is not valid"), "alert-toast")
          });
        }).catch(err => {
          this.vhComponent.hideLoading(0);
          this.vhComponent.showToast(1500, this.lang.translate("Shop key or secret_key is not valid"), "alert-toast")
        })
      })
    }).catch(err => { })
  }
  search(value: string) {
    if (value.length) {
      let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase())
      this.customerShow = this.vhAlgorithm.searchList(val, [...this.listCustomer], ['phone', 'name'])
    } else this.customerShow = [...this.listCustomer]
  }
  /**Kiểm tra shop key và secret key đã tồn tại trong db hay chưa, nếu có tồn tại rồi không cho sửa khách hàng */
  checkShopExist() {
    this.existShop = false;
    this.signInForm.controls.id_customer.setValue('')
    if (this.signInForm.value.secret_key) {
      this.loading = true;
      this.vhQuerySales.getShopPartner(this.signInForm.value.secret_key).then((customer: any) => {
        if (customer) {
          this.signInForm.controls.id_customer.setValue(customer._id)
          this.existShop = true
        }
        this.loading = false
      })
    }
  }
}
